/* ===== HOMEPAGE STYLES ===== */
.homepage {
  flex-grow: 1;
  padding: 2rem;
  position: relative;
  z-index: 0;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.homepage::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 30%, rgba(77, 142, 255, 0.1) 0%, transparent 40%),
    radial-gradient(circle at 80% 70%, rgba(109, 122, 252, 0.08) 0%, transparent 40%);
  pointer-events: none;
  z-index: -1;
  opacity: 0.5;
}

.section-container {
  max-width: 100%;
  margin: 0 auto;
  animation: fadeIn 0.8s ease-out forwards;
}

.section-title {
  color: var(--light-1);
  margin: 1.5rem 0 2rem;
  font-size: 2.2rem;
  font-weight: 800;
  position: relative;
  display: inline-block;
  padding-bottom: 0.5rem;
  letter-spacing: -0.5px;
}

.section-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, var(--primary), var(--secondary));
  border-radius: 4px;
}

.section-content {
  background: var(--glass);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: var(--shadow-md);
  backdrop-filter: blur(16px);
  border: 1px solid var(--glass-border);
  margin-bottom: 2.5rem;
  transition: all var(--transition-normal);
}

.section-content:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(77, 142, 255, 0.3);
}

/* ===== EXPERIENCE TIMELINE ===== */
.experience-timeline {
  position: relative;
  padding-left: 1.5rem;
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  height: calc(100% - 20px);
  width: 3px;
  background: linear-gradient(to bottom, var(--primary), var(--secondary));
  border-radius: 3px;
}

.experience-item {
  position: relative;
  margin-bottom: 2.5rem;
  padding-left: 1.8rem;
  transition: all var(--transition-normal);
  background: var(--glass);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 1.5rem;
  border: 1px solid var(--glass-border);
}

.experience-item:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-sm);
}

.experience-item::before {
  content: '';
  position: absolute;
  left: -0.5rem;
  top: 1.8rem;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--primary);
  border: 3px solid var(--dark-2);
  box-shadow: 0 0 0 4px rgba(77, 142, 255, 0.3);
}

.experience-item h3 {
  font-size: 1.3rem;
  color: var(--light-1);
  margin-bottom: 0.3rem;
}

.company {
  color: var(--primary);
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.duration, .grade {
  color: var(--light-3);
  font-size: 0.95rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.achievements {
  list-style-type: none;
  padding-left: 0;
}

.achievements li {
  margin-bottom: 0.8rem;
  padding-left: 1.5rem;
  position: relative;
  line-height: 1.7;
}

.achievements li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.4em;
  top: -0.1rem;
}

/* ===== SKILLS GRID ===== */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.skill-category {
  transition: all var(--transition-normal);
  background: var(--glass);
  backdrop-filter: blur(10px);
  padding: 1.5rem;
  border-radius: 16px;
  border: 1px solid var(--glass-border);
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
  border-color: rgba(77, 142, 255, 0.3);
}

.skill-category h3 {
  color: var(--primary);
  margin-bottom: 1rem;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
}

.skill-category ul {
  list-style-type: none;
  padding-left: 0;
}

.skill-category li {
  margin-bottom: 0.7rem;
  padding-left: 1.2rem;
  position: relative;
}

.skill-category li::before {
  content: '•';
  color: var(--primary);
  font-weight: bold;
  font-size: 1.4rem;
  position: absolute;
  left: 0;
  top: -0.3rem;
}

/* ===== EDUCATION ===== */
.education-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.education-item {
  background: var(--glass);
  padding: 1.5rem;
  border-radius: 16px;
  border-left: 4px solid var(--primary);
  transition: all var(--transition-normal);
  backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
}

.education-item h3 {
  font-size: 1.3rem;
  color: var(--light-1);
  margin-bottom: 0.5rem;
}

.institution {
  font-weight: 600;
  color: var(--primary);
  font-size: 1.1rem;
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skills {
  margin-top: 1.2rem;
  padding-top: 1.2rem;
  border-top: 1px solid var(--glass-border);
}

.skills h4 {
  font-size: 1rem;
  margin-bottom: 0.8rem;
  color: var(--light-2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skills ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.skills li {
  background: rgba(77, 142, 255, 0.2);
  padding: 0.3rem 0.9rem;
  border-radius: 20px;
  font-size: 0.85rem;
  transition: all 0.2s ease;
  border: 1px solid rgba(77, 142, 255, 0.3);
}

.skills li:hover {
  background: rgba(77, 142, 255, 0.3);
  transform: translateY(-2px);
}

.certifications {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--glass-border);
}

.certifications h3 {
  font-size: 1.3rem;
  color: var(--light-1);
  margin-bottom: 1rem;
}

.certifications ul {
  list-style-type: none;
  padding-left: 0;
}

.certifications li {
  margin-bottom: 0.8rem;
  padding-left: 1.5rem;
  position: relative;
}

.certifications li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: bold;
}

/* ===== RESPONSIVE FIXES ===== */
@media (max-width: 1024px) {
  .homepage {
    padding: 1.5rem;
  }
  
  .section-title {
    font-size: 1.8rem;
    margin: 1rem 0 1.5rem;
  }
  
  .section-content {
    padding: 1.5rem;
  }
}

@media (max-width: 768px) {
  .homepage {
    padding: 1.2rem;
  }
  
  .section-title {
    font-size: 1.6rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .experience-timeline::before {
    left: 0.35rem;
  }
  
  .experience-item::before {
    left: -0.35rem;
  }
}

@media (max-width: 480px) {
  .homepage {
    padding: 1rem;
  }
  
  .section-content {
    padding: 1.2rem;
  }
  
  .section-title {
    font-size: 1.4rem;
  }
  
  .achievements li {
    padding-left: 1.2rem;
  }
}